.promo-code-content {
  display: flex;
  gap: 32px;
}

.promo-code-block {
  display: flex;
  gap: 8px;
}

.promo-code-edit-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  margin-top: auto;
  color: rgb(1, 144, 219);
  transition: color 0.2s;
}
.promo-code-edit-link:active {
  color: rgb(0, 83, 157);
}

.promo-code-edit-icon {
  transition: all 0.2s;
}

.promo-code-block-wrapper {
  display: flex;
  flex-direction: column;
}

.promo-code-label {
  color: #5c5c5c;
  font-size: 16px;
  line-height: 24px;
}

.promo-code-text {
  color: #1f1f1f;
  font-size: 28px;
  line-height: 42px;
}

.promo-code-link {
  color: #006fbc;
  font-size: 14px;
  line-height: 20px;
  text-decoration: none;
}
@media (max-width: 767px) {
  .promo-code-link {
    font-size: 16px;
    line-height: 24px;
  }
}

.price-content {
  display: flex;
  gap: 32px;
}

.price-block {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.price-block-label {
  color: #5c5c5c;
  font-size: 12px;
  line-height: 16px;
}

.price-block-text {
  color: #1f1f1f;
  font-size: 16px;
  line-height: 24px;
}
.price-block-text_active {
  color: #1d8722;
}

.text-title {
  color: #1f1f1f;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}

.index-table {
  grid-template-columns: 88fr 576fr 56fr;
}
.index-table .table-cell-h:nth-child(3) {
  justify-content: center;
}
.index-table .table-row .table-cell:nth-child(3) {
  padding: 4px 8px;
  display: flex;
  align-items: center;
}

.edit-apply-block {
  display: flex;
  gap: 8px;
  padding-top: 16px;
}
@media (max-width: 767px) {
  .edit-apply-block {
    display: grid;
    grid-template-columns: max-content 1fr;
  }
}

.edit-input {
  max-width: 128px;
}
@media (max-width: 767px) {
  .edit-input {
    max-width: none;
  }
}

.statistics-table-wrapper::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.statistics-table-wrapper::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #d6d6d6;
}
.statistics-table-wrapper::-webkit-scrollbar-track {
  background-color: #fff;
  border-radius: 10px;
}
@media (max-width: 767px) {
  .statistics-table-wrapper {
    overflow: auto;
    width: 100vw;
    margin-left: -16px;
    padding: 0 16px;
    padding-bottom: 8px;
  }
}

.statistics-table {
  min-width: 720px;
  grid-template-columns: 136fr 88fr 60fr 112fr 112fr 156fr 56fr;
}
.statistics-table .table-cell-h:nth-child(7) {
  justify-content: center;
}
.statistics-table .table-row .table-cell:nth-child(5) {
  font-weight: 600;
}
.statistics-table .table-row .table-cell:nth-child(7) {
  padding: 4px 8px;
  display: flex;
  align-items: center;
}

.header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 16px;
  padding: 16px 8px;
  margin: 32px 32px 0;
  background-color: #fff;
}
@media (max-width: 1055px) {
  .header {
    margin: 16px 16px 0;
  }
}
@media (max-width: 767px) {
  .header {
    flex-direction: column;
    gap: 24px;
    padding: 0;
    margin: 16px 16px 0;
  }
}

.title-wrapper {
  display: flex;
}

.title {
  display: none;
  margin: 0;
  color: #1f1f1f;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
}

.title-logo {
  display: flex;
  align-items: center;
  gap: 12px;
}
.title-logo__icon {
  width: 40px;
  height: 40px;
}
.title-logo__text-icon {
  width: 108px;
  height: 18px;
}

.user {
  display: flex;
  gap: 8px;
}
@media (max-width: 767px) {
  .user {
    display: grid;
    grid-template-columns: 1fr 40px;
  }
}
.user__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
  flex-grow: 0;
}
@media (max-width: 767px) {
  .user__content {
    width: calc(100vw - 80px);
  }
}
.user__name {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43;
  color: #1f1f1f;
}
@media (max-width: 1055px) {
  .user__name {
    overflow: hidden;
    text-overflow: ellipsis;
    text-wrap: nowrap;
  }
}
.user__email {
  overflow: hidden;
  color: #5c5c5c;
  text-overflow: ellipsis;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.33;
}
.user__button-exit {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  border-radius: 8px;
  background-color: transparent;
  color: #5c5c5c;
  cursor: pointer;
}
.user__button-exit:hover {
  color: #1f1f1f;
}
.user__button-exit:active {
  color: #1f1f1f;
}
.user__button-exit_selected {
  background-color: rgba(0, 0, 0, 0.08);
  color: #5c5c5c;
}
.user__icon-exit {
  fill: currentColor;
}

.table {
  display: grid;
}

.table-head {
  display: contents;
  padding: 8px 0;
  background: #fff;
}
.table-head .table-cell-h:nth-child(1) {
  padding-left: 0;
}

.table-cell-h {
  display: flex;
  align-items: center;
  padding: 12px 8px;
  border-bottom: 2px solid #d6d6d6;
  color: #1f1f1f;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
}

.table-row {
  display: contents;
  padding: 4px 0;
}
.table-row .table-cell:nth-child(1) {
  padding-left: 0;
}

.table-cell {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #d6d6d6;
  padding: 12px 8px;
  color: #1f1f1f;
  font-size: 14px;
  line-height: 20px;
}
.table-cell.align-start {
  align-items: flex-start;
}
.table-cell.ellipsis {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.table-button {
  padding: 0;
  border: none;
  background-color: transparent;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.table-icon {
  width: 24px;
  height: 24px;
  fill: #5c5c5c;
}

.tooltip {
  position: absolute;
  top: -50%;
  left: 0;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  z-index: 900;
  width: max-content;
  padding: 8px 12px;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.64);
  font-size: 14px;
  line-height: 1.43;
  color: #fff;
  opacity: 0;
}
.tooltip__arrow {
  position: absolute;
  left: 14px;
  bottom: -6px;
  width: 12px;
  height: 6px;
  fill: rgba(0, 0, 0, 0.64);
}

.tooltip.tooltip_left {
  left: auto;
  right: 0;
}
.tooltip.tooltip_left .tooltip__arrow {
  left: auto;
  right: 14px;
}

.tooltip.tooltip_bottom {
  top: auto;
  bottom: 0;
  transform: translateY(calc(100% + 10px));
}
.tooltip.tooltip_bottom .tooltip__arrow {
  transform: rotate(180deg);
  bottom: auto;
  top: -6px;
}

.tooltip-trigger {
  position: relative;
}

.tooltip-trigger:hover .tooltip {
  visibility: visible;
  opacity: 1;
}

.text-input {
